<template>
    <div class="writecom">
         <div class="box">
           <div class="outgo-box" @click="goPrenext">
                <van-icon  name="arrow-left" size="25" />
           </div>
           <div class="mi-cont">
               <span>{{msg}}</span>
           </div>
           <div class="but" @click="ropaly">
               <span>发布</span>
           </div>
        </div>
        <div class="">
            <van-cell-group>
                <van-field v-model="value" label="" placeholder="友善的评论是交流的起点" />
            </van-cell-group>
        </div>
    </div>
</template>

<script>
    export default {
        name:'writecom',
        data(){
            return{
                value:'',
                msg:'',
                index:'',
                ropalyName:'',
                mysendstr:false,
                imgStr: require('../assets/rto.png'),
                nan:'',
            }
        },
        created(){
            let str = JSON.parse(localStorage.getItem('str'));
            let nan = JSON.parse(localStorage.getItem('preser'))||{};
           
            if(nan !== {}){
                this.nan = nan.prename
            }else{
                this.nan = '我'
            }
            if(str.length >= 1){
                // console.log(str[str.length-1]);
                this.imgStr = str[str.length-1].url
            }
            let my = this.$route.params.my;
            // console.log(my);
            this.mysendstr = my
            this.ropalyName = this.$route.params.m
            this.index = this.$route.params.index
            if(!my){
                this.msg = '写评论'
                // console.log(this.$route.params);
            }else{
                this.msg = '回复' + this.ropalyName
            }
            
        },
        methods:{
            // 返回上一级
            goPrenext(){
                this.$router.go(-1)
            },
            // 写评论
            ropaly(){
                let len = JSON.parse(localStorage.getItem('len')) || [];
                // console.log(len);
                if(!this.mysendstr){
                    // console.log();
                    console.log('youl');
                    let obj = {
                        author:this.nan,
                        avatar:this.imgStr,
                        content:this.value,
                        likes:0,
                        time:Date.now(),
                    }
                    len.unshift(obj)
                    // console.log(obj);
                }else{
                    // console.log('123');
                    let message = `我${this.msg}:${this.value}`;
                    for(let i = 0; i<len.length;i++){
                        len[this.index].ropalytext = message;
                        
                    }
                }
                localStorage.setItem('len',JSON.stringify(len))

                this.$router.push({name:'commentpge'})

                this.value = ''
            }
        }
    }
</script>

<style lang="less" scoped>
.writecom{
    width: 100%;
}
.box{
    width: calc(100% - 15px);
    height: 50px;
    border-bottom: 1px solid rgba(143, 140, 140, 0.2);
    padding: 0 10px 0 5px;
    display: flex;
    align-items: center;
    div{
        flex: 1;
    }
    .mi-cont{
        text-align: center;
    }
    .but{
        text-align: right;
    }
}
</style>